<script>
  import { onMount } from 'svelte';

  import Chart from './Chart.svelte';
  import {
    formatData,
  } from '../utils';

  export let opcao;
  export let key;
  let open = false;
  let totalHistory = 0;

  const toggle = () => {
    open = !open;
  };

  const openCard = () => {
    open = true;
  };

  const getDataChart = (history, key) => {
    const data = history.map((item) => {
      return item[key];
    });
    return data;
  };

  const getLabelChart = (history) => {
    const dates = history.map((item) => {
      return formatData(item.data);
    });
    return dates;
  };

  const getDatasetChart = (history, type) => {
    if (type === 'price') {
      return [{
        data: getDataChart(history, 'fechamento'),
        type: 'line',
        label: 'Preço',
        lineTension: 0,
        borderWidth: 1,
        backgroundColor: 'rgba(255, 99, 132, 0.2)',
        borderColor: 'rgba(255, 99, 132, 1)',
      }];
    }

    return [{
      data: getDataChart(history, 'quantidade'),
      type: 'bar',
      label: 'Quantidade',
      borderWidth: 1,
      backgroundColor: 'rgba(0, 0, 0, 0.2)',
      borderColor: '#8c8a8a',
    }];
  };

  onMount(() => {
    // abre card caso tem histórico maior que 3 itens
    let countValidHistory = 0;
    opcao.historico.forEach((item) => {
      if (item.fechamento) {
        countValidHistory++;
      }
    });

    if (countValidHistory > 2) {
      openCard();
    }

  });

</script>

<style>
  .container {
    font-size: 13px;
    padding: 5px 20px;
    border: 1px solid #eee;
  }

  .container.active {
    border-color: #5105e4;
  }

  .header {
    color: #8e8e94;
    display: flex;
    cursor: pointer;
    font-weight: bold;
    justify-content: space-between;
  }

  .header.active {
    color: #5105e4;
  }

  .chart {
    margin-top: 20px;
  }
</style>

<div class="container" class:active="{open === true}">
  <div class="header" class:active="{open === true}" on:click={toggle}>
    <strong>{opcao.codigo} ({opcao.acao})</strong>
    <strong>Preço: {opcao.preco}</strong>
    <strong>Vencimento: {opcao.vencimento}</strong>
  </div>

  {#if open}
    <div class="chart">
      <Chart
        key={`chart-${key}-1`}
        datasets={getDatasetChart(opcao.historico, 'price')}
        labels={getLabelChart(opcao.historico)}
      />
      <Chart
        key={`chart-${key}-2`}
        datasets={getDatasetChart(opcao.historico, 'volume')}
        labels={getLabelChart(opcao.historico)}
      />
    </div>
  {/if}
</div>
