<script>
  import { onMount } from 'svelte';
  import querystring from 'query-string';

  import Opcao from './Opcao.svelte';
  import FormSearch from './FormSearch.svelte';
  import {
    fetchApi,
    formatData,
  } from '../utils';

  let QTD_FETCH = 250;

  let totalOpcoes = 0;
  let loading = false;
  let opcoes = [];
  let stock = '';
  let stockMonth = '';

  const searchParams = {
    tipo: 'venda',
    qtd: QTD_FETCH,
    inicio: 0,
  };

  const runTimeoutOpcoes = async (initDoc = 0) => {
    console.log('runTimeoutOpcoes');
    searchParams.inicio = initDoc;
    const path = `/opcoes/completo?${querystring.stringify(searchParams)}`;
    console.log('path', path);

    const res = await fetchApi(path);
    // console.log('res', res);
    opcoes = [...opcoes, ...res];

    if (initDoc <= totalOpcoes) {
      setTimeout(() => {
        runTimeoutOpcoes(initDoc + QTD_FETCH);
      }, 1000);      
    } else {
      loading = false;
    }
  };

  const getOpcoes = async (form) => {
    const params = {
      tipo: form.type || 'venda',
    };
    stock = form.stock || '';
    stockMonth = form.stockMonth || '';
    console.log('getOpcoes');
    console.log('params', params);

    const res = await fetchApi(`/opcoes/total?${querystring.stringify(params)}`);
    totalOpcoes = res[0].total;

    opcoes = [];
    loading = true;
    searchParams.tipo = params.tipo;

    runTimeoutOpcoes();
  };

  const hideOpcao = (opcao) => {
    if (stockMonth === '' && stock === '') {
      return false;
    }

    let hide = false;

    if (stockMonth !== '' && stockMonth !== opcao.vencimento) {
      hide = true;
    }

    if (stock !== '' && opcao.codigo.match(stock) === null) {
      hide = true;
    }

    return hide;
  };

</script>

<style>
  .container {
    padding: 20px;
    margin: 0 auto;
    max-width: 800px;
    border: 1px solid #dedede;
  }

  .filter {
    margin-top: 20px;
    font-size: 12px;
    font-weight: bold;
  }

  h3 {
    font-size: 12px;
    margin-top: 20px;
    font-weight: bold;
  }

  .list {
    margin-top: 25px;
  }

  .list .hide {
    display: none;
  }
</style>

<div class="container">
  <FormSearch onGetOpcoes={getOpcoes} />

  <h3>
    Status:
    {#if loading}
      Carregando opções... Total de { totalOpcoes } / Opções carregadas: { opcoes.length }
    {:else if !loading && opcoes.length}
      Lista carregada :) - Total de opções carregadas: { opcoes.length }
    {:else}
      Aguardando iniciar a busca
    {/if}
  </h3>

  <ul class="list">
    {#each opcoes as opcao, index}
      <li class:hide="{hideOpcao(opcao)}">
        <Opcao
          key={`opcao-${index}`}
          opcao={opcao}
        />
      </li>
    {/each}
  </ul>
</div>
